<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false
    };
  },
  computed:{
    sratio(){
      let proportion=1-this.itemData.clearance*0.007;
      return `transform: scale(${proportion})`
    },
    sratios(){
      let proportion=1-this.itemData.clearance*0.0033;
      return `transform: scale(${proportion})`
    }
  },
  methods: {
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>

